body
{
    width: 100%;
}
.container {
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .inputContainer h1 {
    font-family: 'Playfair Display', serif;
    font-size: 3.5rem;
    color: #fff;
    margin-bottom: 1.5rem;
  }
  .inputContainer h1 span {
    color: var(--primary-color);
  }
  .inputContainer div {
    display: flex;
    margin-bottom: 2rem;
  }
  .inputContainer input {
    width: 100%;
    padding: 0.5rem 0.2rem 0.5rem 0;
    text-indent: 1rem;
    border: none;
    outline: none;
    font-size: 1rem;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  .inputContainer input::-webkit-input-placeholder {
    font-size: 0.9rem;
  }
  .inputContainer input::placeholder {
    font-size: 0.9rem;
  }
  .inputContainer button {
    padding: 0 1rem;
    height: 3rem;
    font-size: 0.8rem;
    text-transform: uppercase;
    font-weight: bold;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    color: rgb(93, 211, 57);
    
    border: none;
    cursor: pointer;
  }
  
  /* Result */
  .result {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem;
  }
  .result p {
    color: #fff;
    border: 1px solid var(--primary-color);
    padding: 0.5rem 1rem;
    margin-right: 1rem;
  }
  .result button {
    padding: 0.5rem 1rem;
    color: rgb(106, 201, 68);
  }
  .result button.copied {
    background: rgb(233, 128, 7);
    color: var(--primary-color);
  }
  .noData {
    color: #fff;
    font-size: 1rem;
  }
  
  /* background animate */
  @-webkit-keyframes animate {
    0%{
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100%{
        -webkit-transform: translateY(-1000px) rotate(720deg);
                transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
  }
  @keyframes animate {
    0%{
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100%{
        -webkit-transform: translateY(-1000px) rotate(720deg);
                transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
  }
  
  .background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    background: #4e54c8;
    overflow: hidden;
    z-index: -1;
  }
  .background li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    -webkit-animation: animate 19s linear infinite;
            animation: animate 19s linear infinite;
  }
  
  
  
  
  .background li:nth-child(0) {
    left: 65%;
    width: 97px;
    height: 97px;
    bottom: -97px;
    -webkit-animation-delay: 1s;
